.navbar{
  background: #232323;
  color: #ffffff;
  position: sticky;
  top: 0;
  z-index: 100;
  transition: .3s all ease;
  font-family: $font-ubuntu;

  .navbar-brand{
    font-size: 2.3rem;
    letter-spacing: .1rem;
    font-weight: 600;
  }



  .navbar-nav{
    .nav-item{
      padding: 0 1.5rem;
      .nav-link{
        color: #ffffff;
        font-size: 1.4rem;
        padding: 0 .5rem;
        box-sizing: border-box;
        cursor: pointer;
        &:hover{
          color: #ffffffb3;
        }
      }
    }
  }

  .dropdown{
    &-menu{
      min-width: 15rem;
    }
    .dropdown-item{
      font-size: 1.4rem;
      color: #ffffff;
      text-align: center;
      cursor: pointer;
      &:hover{
        color: black;
      }
    }
  }


}
